import React, { Component } from "react";
import Article from "../elements/Article";

class Articles extends Component {
  constructor(props) {
    super();
    this.state = { articles: [] };
  }

  componentDidMount() {
    let articles = [];

    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="SkyMNIST: The World's First Self-Reprogramming AI"
          url="https://alexsheng.com/skymnist.html"
          image="images/AlexPolo.jpg"
          heading="Self-Publication"
          extract="A story of the holiday break when I invented self-reprogramming artificial intelligence. This is a casual recount of my experience, with a cursory overview of how the technology works."
        />
      </div>
    )
    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="Distributed Evolution Strategies Using TPUs for Meta-Learning"
          url="https://ieeexplore.ieee.org/document/9308334"
          image="images/AlexPolo.jpg"
          heading="IEEE Conference Paper"
          extract="I developed a distributed evolutionary algorithm for optimizing neural networks on clusters of Google's AI supercomputers. I designed ES-ProtoNet, a variation of the prototypical network for few-shot learning that utilizes my distributed evolution strategies algorithm."
        />
      </div>
    )
    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="IBM Domain Adaptation Paper"
          url=""
          image="images/AlexPolo.jpg"
          heading="Upcoming Conference Paper"
          extract="This paper is currently in anonymity. A manuscript will be available soon!"
        />
      </div>
    )

    this.setState({ articles: articles });
  }

  render() {
    return (
      <section className="section" id="articles">
        <div className="container">
          <h1 className="title">Publications</h1>
          <h2 className="subtitle is-4">My research papers and personal articles</h2>
          <div className="columns">{this.state.articles}</div>
        </div>
      </section>
    );
  }
}

export default Articles;
